
.nav-list{background-color:#21364D;line-height:20px;color:#FFF;min-height:100vh;border-right:2px outset #0C2A4B
}
.nav-list .nav-title{height:45px;padding-left:16px;background-color:#0C2A4B;font-size:20px;line-height:45px;color:#FFF;position:fixed;width:229px;z-index:99;top:0;left:0
}
.nav-list a{color:#CCC
}
.nav-list .router-list{padding-top:45px
}
.nav-list .router-list a{color:#CCC;display:block;font-size:12px;line-height:40px;padding-left:40px
}
.nav-list .router-list a:hover,.nav-list .router-list a.router-link-active,.nav-list .router-list a.active{background-color:#2E4656;color:#08f18c;border-right:2px solid #15dc86;margin-right:-2px
}
.nav-list .router-list a:hover span,.nav-list .router-list a.router-link-active span,.nav-list .router-list a.active span{display:inline
}
.nav-list b{min-width:8em;display:inline-block
}
.nav-list a span{color:#08f18c;display:none
}
.nav-list h2{line-height:30px;padding-left:10px;margin:10px 0 0 0;color:#CCC;font-size:14px !important;position:relative
}
.nav-list h2 i{margin-right:10px;position:absolute;font-size:10px;right:10px;top:10px;transform:scale(.7)
}


