
.test-form{height:300px
}
.test-form input,.test-form textarea{width:150px;line-height:20px
}
.test-form form{border:1px solid #CCC;border-radius:5px;padding:10px;background-color:#FFF
}
.test-form form:hover{box-shadow:3px 5px #EEE
}
.test-form .line{line-height:30px;padding:5px 10px
}
.test-form .line label{width:5em;display:inline-block;vertical-align:top
}


