
.smui-progress progress{width:160px;height:20px;border:1px solid #0064B4;background-color:#e6e6e6;color:#0064B4
}
.smui-progress progress::-moz-progress-bar{background:#0064B4
}
.smui-progress progress::-webkit-progress-bar{background:#FFF
}
.smui-progress progress::-webkit-progress-value{background:#0064B4
}
.smui-progress .bar{background-color:deepskyblue;height:10px;border-radius:5px
}
.smui-progress .progress-box{border:1px solid #EEE;border-radius:5px;background-color:#EEE;width:100%;display:block
}


