
.fonticon-list li{display:inline-block;padding:10px;width:150px;height:50px;margin:10px;vertical-align:top;border:1px solid #EEE;position:relative
}
.fonticon-list .xicon{display:none;position:absolute;color:#26C890;height:100%;width:100%;top:0;left:0;background:#FFF;text-align:center;line-height:50px;font-size:3em
}
.fonticon-list li:hover .xicon{display:block
}


