
.fixed-dialog .dialog.f{position:fixed;top:0;left:0;height:100vh;width:100vw;overflow:hidden
}
.fixed-dialog .dialog.f>div>.content{padding:0
}
.fixed-dialog .dialog.f>div>.footer{position:fixed;background:#EEE;z-index:99
}
.top-nav{margin-left:230px;height:45px;position:fixed;background:#FFF;width:100%;z-index:777;left:0;top:0;border-bottom:1px solid #DDD;margin-top:0
}
.top-nav .tab-list{border:none
}
.top-nav .tab-list .tab a{height:45px
}
.top-nav .comp-name{font-size:18px;font-weight:bold;line-height:40px;padding:10px 20px
}
.top-nav .doc-link{position:fixed;right:11px;top:13px;margin:0 10px;z-index:9999;right:50px;float:right;color:#333
}
.mleft{position:fixed;top:0;left:0;width:230px;border-right:1px solid #CCC;height:100vh;overflow:hidden
}
.mleft:hover{overflow:scroll
}
.mright{margin-left:250px;margin-top:80px
}
.mright .scrum{padding-left:10px;height:24px
}


