
.cssicon-list .icon,.cssicon-list .icon *{box-sizing:content-box !important
}
.cssicon-list li{display:inline-block;padding:10px;width:150px;height:85px;margin:10px;text-align:center;margin:10px 20px;vertical-align:top;border:1px solid #EEE;position:relative;cursor:pointer
}
.cssicon-list .notation{margin-top:30px;text-align:center
}
.cssicon-list .xicon{display:none;position:absolute;color:#26C890;height:100%;width:100%;top:0;left:0;background:#FFF;text-align:center;line-height:50px;font-size:3em
}
.cssicon-list li:hover .xicon{display:block
}


