
@-webkit-keyframes boxspin{
0%{-webkit-transform:rotateX(0deg) rotateY(0deg) rotateZ(0deg);-webkit-transform-origin:center center
}
100%{-webkit-transform:rotateX(180deg) rotateY(180deg) rotateZ(180deg);-webkit-transform-origin:center center
}
}
@-moz-keyframes boxspin{
0%{-moz-transform:rotateX(0deg) rotateY(0deg) rotateZ(0deg);-webkit-transform-origin:center center
}
100%{-moz-transform:rotateX(180deg) rotateY(180deg) rotateZ(180deg);-webkit-transform-origin:center center
}
}
.home-page{position:relative;padding:200px 50px
}
.home-page *{margin:0;padding:0
}
.home-page .wrap{height:100%;position:relative;-webkit-transform-style:preserve-3d;-webkit-perspective:0;-moz-transform-style:preserve-3d;-moz-perspective:0;-webkit-animation:boxspin 5s ease infinite;-moz-animation:boxspin 5s ease infinite
}
.home-page .box{width:200px;height:200px;position:absolute;top:50%;left:50%;margin:-100px 0 0 -100px;line-height:200px;text-align:center;font-size:48px;color:white
}
.home-page .box1{-webkit-transform:rotatey(90deg) translatez(-100px);-moz-transform:rotatey(90deg) translatez(-100px);background:rgba(128,0,128,0.5)
}
.home-page .box2{-webkit-transform:rotatey(90deg) translatez(100px);-moz-transform:rotatey(90deg) translatez(100px);background:rgba(255,0,255,0.5)
}
.home-page .box3{-webkit-transform:rotatex(90deg) translatez(100px);-moz-transform:rotatex(90deg) translatez(100px);background:rgba(255,153,204,0.5)
}
.home-page .box4{-webkit-transform:rotatex(90deg) translatez(-100px);-moz-transform:rotatex(90deg) translatez(-100px);background:rgba(0,204,255,0.5)
}
.home-page .box5{-webkit-transform:translatez(-100px);-moz-transform:translatez(-100px);background:rgba(153,204,255,0.5)
}
.home-page .box6{-webkit-transform:translatez(100px);-moz-transform:translatez(100px);background:rgba(0,255,255,0.5)
}


